import axios from 'axios';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:4000/api';

export const apiClient = axios.create({
    baseURL: API_BASE_URL,
    headers: {
        'Content-Type': 'application/json',
    },
    withCredentials: true,
});

// Blog API
export const blogAPI = {
    getAll: (params?: any) => apiClient.get('/blog', { params }),
    getOne: (identifier: string) => apiClient.get(`/blog/${identifier}`),
    create: (data: any) => apiClient.post('/blog', data),
    update: (code: string, data: any) => apiClient.put(`/blog/${code}`, data),
    delete: (code: string) => apiClient.delete(`/blog/${code}`),
};

// Products API
export const productsAPI = {
    getAll: (params?: any) => apiClient.get('/products', { params }),
    getOne: (identifier: string) => apiClient.get(`/products/${identifier}`),
    create: (data: any) => apiClient.post('/products', data),
    update: (code: string, data: any) => apiClient.put(`/products/${code}`, data),
    delete: (code: string) => apiClient.delete(`/products/${code}`),
};

// Auth API
export const authAPI = {
    login: (username: string, password: string) =>
        apiClient.post('/auth/login', { username, password }),
    logout: () => apiClient.post('/auth/logout'),
    changePassword: (currentPassword: string, newPassword: string) =>
        apiClient.post('/auth/change-password', { currentPassword, newPassword }),
    getMe: () => apiClient.get('/auth/me'),
};

export default apiClient;
